import React, { createContext, useContext, useState, useEffect } from 'react';

const AuthContext = createContext();

export function useAuth() {
  return useContext(AuthContext);
}

export function AuthProvider({ children }) {
  const [currentUser, setCurrentUser] = useState(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    // Check for existing user session
    const storedUser = localStorage.getItem('orbit_user');
    if (storedUser) {
      setCurrentUser(JSON.parse(storedUser));
    }
    setLoading(false);
  }, []);

  const login = async (email, password) => {
    try {
      // Get existing users from localStorage
      const users = JSON.parse(localStorage.getItem('orbit_users') || '[]');
      
      // Check if user exists
      const user = users.find(u => u.email === email && u.password === password);
      
      if (user) {
        const userSession = {
          id: user.id,
          email: user.email,
          name: user.name
        };
        
        setCurrentUser(userSession);
        localStorage.setItem('orbit_user', JSON.stringify(userSession));
        return { success: true };
      } else {
        return { success: false, error: 'Invalid email or password' };
      }
    } catch (error) {
      return { success: false, error: 'Login failed' };
    }
  };

  const signup = async (name, email, password) => {
    try {
      // Get existing users from localStorage
      const users = JSON.parse(localStorage.getItem('orbit_users') || '[]');
      
      // Check if user already exists
      if (users.find(u => u.email === email)) {
        return { success: false, error: 'User already exists' };
      }
      
      // Create new user
      const newUser = {
        id: window.crypto && window.crypto.randomUUID ? window.crypto.randomUUID() : 'user-' + Date.now(),
        name,
        email,
        password,
        createdAt: new Date().toISOString()
      };
      
      users.push(newUser);
      localStorage.setItem('orbit_users', JSON.stringify(users));
      
      // Auto-login after signup
      const userSession = {
        id: newUser.id,
        email: newUser.email,
        name: newUser.name
      };
      
      setCurrentUser(userSession);
      localStorage.setItem('orbit_user', JSON.stringify(userSession));
      
      return { success: true };
    } catch (error) {
      return { success: false, error: 'Signup failed' };
    }
  };

  const logout = () => {
    setCurrentUser(null);
    localStorage.removeItem('orbit_user');
  };

  const value = {
    currentUser,
    login,
    signup,
    logout,
    loading
  };

  return (
    <AuthContext.Provider value={value}>
      {!loading && children}
    </AuthContext.Provider>
  );
}
