import React, { useState, useEffect } from 'react';
import './App.css';
import ExpenseForm from './components/ExpenseForm';
import ExpenseList from './components/ExpenseList';
import Analytics from './components/Analytics';
import Notification from './components/Notification';
import ConfirmModal from './components/ConfirmModal';
import FloatingAddButton from './components/FloatingAddButton';
import ProtectedRoute from './components/ProtectedRoute';
import { AuthProvider, useAuth } from './contexts/AuthContext';

function App() {
  return (
    <AuthProvider>
      <AppContent />
    </AuthProvider>
  );
}

function AppContent() {
  const { currentUser, logout } = useAuth();
  const [expenses, setExpenses] = useState([]);
  const [filteredExpenses, setFilteredExpenses] = useState([]);
  const [timeFilter, setTimeFilter] = useState('all');
  const [notification, setNotification] = useState(null);
  const [showConfirm, setShowConfirm] = useState(false);
  const [confirmCallback, setConfirmCallback] = useState(null);
  const [showExpenseForm, setShowExpenseForm] = useState(false);
  const [showUserDropdown, setShowUserDropdown] = useState(false);
  const [showLogoutConfirm, setShowLogoutConfirm] = useState(false);

  const chartColors = {
    primary: ['#6366f1', '#38bdf8', '#ef4444', '#10b981', '#f59e0b', '#8b5cf6', '#ec4899', '#14b8a6'],
    gradients: [
      'rgba(99, 102, 241, 0.8)',
      'rgba(56, 189, 248, 0.8)',
      'rgba(239, 68, 68, 0.8)',
      'rgba(16, 185, 129, 0.8)',
      'rgba(245, 158, 11, 0.8)',
      'rgba(139, 92, 246, 0.8)',
      'rgba(236, 72, 153, 0.8)',
      'rgba(20, 184, 166, 0.8)'
    ]
  };

  // Load expenses from localStorage on mount
  useEffect(() => {
    if (currentUser) {
      const stored = localStorage.getItem(`orbit_expenses_${currentUser.id}`);
      const data = stored ? JSON.parse(stored) : [];
      setExpenses(data);
    }
  }, [currentUser]);

  // Apply filters whenever expenses or timeFilter changes
  useEffect(() => {
    applyFilters();
  }, [expenses, timeFilter]);

  const filterExpensesByTime = (expenseList, period) => {
    const now = new Date();
    const today = new Date(now.getFullYear(), now.getMonth(), now.getDate());

    switch (period) {
      case 'week':
        const weekAgo = new Date(today);
        weekAgo.setDate(weekAgo.getDate() - 7);
        return expenseList.filter(e => new Date(e.date) >= weekAgo);

      case 'month':
        const monthStart = new Date(now.getFullYear(), now.getMonth(), 1);
        return expenseList.filter(e => new Date(e.date) >= monthStart);

      case 'year':
        const yearStart = new Date(now.getFullYear(), 0, 1);
        return expenseList.filter(e => new Date(e.date) >= yearStart);

      case 'all':
      default:
        return expenseList;
    }
  };

  const applyFilters = () => {
    const filtered = filterExpensesByTime(expenses, timeFilter);
    setFilteredExpenses(filtered);
  };

  const saveExpenses = (updatedExpenses) => {
    if (currentUser) {
      localStorage.setItem(`orbit_expenses_${currentUser.id}`, JSON.stringify(updatedExpenses));
      setExpenses(updatedExpenses);
    }
  };

  const addExpense = (newExpense) => {
    const expense = {
      id: window.crypto && window.crypto.randomUUID ? window.crypto.randomUUID() : 'id-' + Date.now(),
      ...newExpense
    };
    const updated = [...expenses, expense];
    saveExpenses(updated);
    showNotification('Expense added successfully', 'success');
  };

  const deleteExpense = (id) => {
    setConfirmCallback(() => () => {
      const updated = expenses.filter(e => e.id !== id);
      saveExpenses(updated);
      showNotification('Expense deleted', 'info');
    });
    setShowConfirm(true);
  };

  const showNotification = (message, type = 'info') => {
    setNotification({ message, type });
    setTimeout(() => setNotification(null), 3000);
  };

  const handleConfirm = (confirmed) => {
    setShowConfirm(false);
    if (confirmed && confirmCallback) {
      confirmCallback();
    }
  };

  const handleExportPDF = async () => {
    const { jsPDF } = window.jspdf;
    const doc = new jsPDF();

    // Title
    doc.setFontSize(22);
    doc.setTextColor(99, 102, 241);
    doc.text("Orbit Analytics Report", 105, 20, null, null, "center");

    doc.setFontSize(12);
    doc.setTextColor(100);
    doc.text(`Generated on: ${new Date().toLocaleDateString()}`, 105, 30, null, null, "center");

    // Transactions Table
    doc.text("Recent Transactions", 14, 45);

    const tableBody = expenses.map(e => [
      e.date,
      e.description,
      e.category,
      `Rs. ${e.amount.toLocaleString('en-IN')}`
    ]);

    doc.autoTable({
      startY: 50,
      head: [['Date', 'Description', 'Category', 'Amount']],
      body: tableBody,
      theme: 'grid',
      headStyles: { fillColor: [99, 102, 241] },
      styles: { fontSize: 10 }
    });

    // Save File Logic
    try {
      if (window.showSaveFilePicker) {
        const handle = await window.showSaveFilePicker({
          suggestedName: 'orbit-report.pdf',
          types: [{
            description: 'PDF Document',
            accept: { 'application/pdf': ['.pdf'] },
          }],
        });
        const writable = await handle.createWritable();
        await writable.write(doc.output('blob'));
        await writable.close();
        showNotification('Report saved successfully', 'success');
      } else {
        doc.save("orbit-report.pdf");
        showNotification('Report downloaded', 'success');
      }
    } catch (err) {
      if (err.name !== 'AbortError') {
        console.error('Failed to save file:', err);
        showNotification('Failed to save file', 'error');
      }
    }
  };

  const handleLogoutClick = () => {
    setShowUserDropdown(false);
    setShowLogoutConfirm(true);
  };

  const confirmLogout = () => {
    setShowLogoutConfirm(false);
    logout();
  };

  const cancelLogout = () => {
    setShowLogoutConfirm(false);
  };

  return (
    <ProtectedRoute>
      <div className="container">
        <header>
          <div className="header-content">
            <div className="logo-area">
              <h1>Orbit <span className="accent">Tracker</span></h1>
              <p>Cosmic Expense Analytics</p>
              <div className="user-info">
                <div className="user-avatar">
                  <svg width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                    <path d="M20 21v-2a4 4 0 0 0-4-4H8a4 4 0 0 0-4 4v2"></path>
                    <circle cx="12" cy="7" r="4"></circle>
                  </svg>
                </div>
                <div className="user-dropdown">
                  <button 
                    className="user-dropdown-trigger" 
                    onClick={() => setShowUserDropdown(!showUserDropdown)}
                  >
                    <span className="user-name">{currentUser?.name}</span>
                    <svg className="dropdown-arrow" width="20" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="4">
                      <polyline points="6,9 12,15 18,9"></polyline>
                    </svg>
                    
                  </button>
                  {showUserDropdown && (
                    <div className="user-dropdown-menu">
                      <button className="dropdown-item logout-item" onClick={handleLogoutClick}>
                        <svg width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                          <path d="M9 21H5a2 2 0 0 1-2-2V5a2 2 0 0 1 2-2h4"></path>
                          <polyline points="16,17 21,12 16,7"></polyline>
                          <line x1="21" y1="12" x2="9" y2="12"></line>
                        </svg>
                        Logout
                      </button>
                    </div>
                  )}
                </div>
              </div>
            </div>
            <div className="header-actions">
              <button className="btn-secondary" onClick={handleExportPDF}>Export PDF</button>
              
            </div>
          </div>
        </header>

        <main>
          <Analytics 
            expenses={filteredExpenses} 
            timeFilter={timeFilter}
            onTimeFilterChange={setTimeFilter}
            chartColors={chartColors}
            showNotification={showNotification}
          />
          <ExpenseList 
            expenses={filteredExpenses} 
            onDeleteExpense={deleteExpense}
          />
        </main>

        <FloatingAddButton onClick={() => setShowExpenseForm(true)} />
        
        <ExpenseForm 
          isOpen={showExpenseForm} 
          onClose={() => setShowExpenseForm(false)}
          onAddExpense={addExpense}
        />

        {notification && (
          <Notification 
            message={notification.message} 
            type={notification.type}
          />
        )}

        {showLogoutConfirm && (
          <div className="modal logout-confirm-modal visible">
            <div className="modal-content">
              <div className="modal-header">
                <h2>Confirm Logout</h2>
                <button className="modal-close-btn" onClick={cancelLogout} aria-label="Close">
                  <svg width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                    <line x1="18" y1="6" x2="6" y2="18"></line>
                    <line x1="6" y1="6" x2="18" y2="18"></line>
                  </svg>
                </button>
              </div>
              <div className="modal-body">
                <p>Are you sure you want to logout?</p>
                <div className="modal-actions">
                  <button className="btn-secondary" onClick={cancelLogout}>Cancel</button>
                  <button className="btn-danger" onClick={confirmLogout}>Logout</button>
                </div>
              </div>
            </div>
          </div>
        )}
      </div>
    </ProtectedRoute>
  );
}

export default App;
