import React from 'react';

function StatisticsCards({ expenses }) {
  const formatter = new Intl.NumberFormat('en-IN', {
    style: 'currency',
    currency: 'INR'
  });

  const total = expenses.reduce((sum, e) => sum + (e.amount || 0), 0);
  const average = expenses.length > 0 ? total / expenses.length : 0;
  const highest = expenses.length > 0 ? Math.max(...expenses.map(e => e.amount || 0)) : 0;
  const count = expenses.length;

  return (
    <div className="stats-grid">
      <div className="stat-card glass-panel">
        <div className="stat-icon"></div>
        <div className="stat-content">
          <div className="stat-label">Total Spent</div>
          <div className="stat-value">{formatter.format(total)}</div>
        </div>
      </div>

      <div className="stat-card glass-panel">
        <div className="stat-icon"></div>
        <div className="stat-content">
          <div className="stat-label">Average</div>
          <div className="stat-value">{formatter.format(average)}</div>
        </div>
      </div>

      <div className="stat-card glass-panel">
        <div className="stat-icon"></div>
        <div className="stat-content">
          <div className="stat-label">Highest</div>
          <div className="stat-value">{formatter.format(highest)}</div>
        </div>
      </div>

      <div className="stat-card glass-panel">
        <div className="stat-icon"></div>
        <div className="stat-content">
          <div className="stat-label">Count</div>
          <div className="stat-value">{count}</div>
        </div>
      </div>
    </div>
  );
}

export default StatisticsCards;
