import React from 'react';

function CategoryBreakdown({ expenses, chartColors }) {
  const formatter = new Intl.NumberFormat('en-IN', {
    style: 'currency',
    currency: 'INR'
  });

  const categories = {};
  expenses.forEach(e => {
    categories[e.category] = (categories[e.category] || 0) + (e.amount || 0);
  });

  const total = expenses.reduce((sum, e) => sum + (e.amount || 0), 0);
  const sortedCategories = Object.entries(categories).sort((a, b) => b[1] - a[1]);

  return (
    <div className="glass-panel chart-container" style={{ marginTop: '2rem' }}>
      <h3>Category Breakdown</h3>
      <div className="category-breakdown" id="category-breakdown">
        {sortedCategories.length === 0 ? (
          <div className="no-data">No expenses to display</div>
        ) : (
          sortedCategories.map(([category, amount], index) => {
            const percentage = ((amount / total) * 100).toFixed(1);
            const colorIndex = index % chartColors.primary.length;

            return (
              <div key={category} className="category-item">
                <div className="category-info">
                  <div 
                    className="category-color" 
                    style={{ background: chartColors.primary[colorIndex] }}
                  ></div>
                  <div className="category-name">{category}</div>
                </div>
                <div className="category-stats">
                  <div className="category-amount">{formatter.format(amount)}</div>
                  <div className="category-percentage">{percentage}%</div>
                </div>
                <div className="category-bar">
                  <div 
                    className="category-bar-fill" 
                    style={{
                      width: `${percentage}%`,
                      background: chartColors.primary[colorIndex]
                    }}
                  ></div>
                </div>
              </div>
            );
          })
        )}
      </div>
    </div>
  );
}

export default CategoryBreakdown;
