import React, { useState, useRef } from 'react';
import emailjs from '@emailjs/browser';
import Notification from './Notification';

function EmailVerification({ email, onVerified, onBack }) {
  const [verificationCode, setVerificationCode] = useState('');
  const [isResending, setIsResending] = useState(false);
  const [error, setError] = useState('');
  const [notification, setNotification] = useState(null);
  const [currentCode, setCurrentCode] = useState(null);
  const hasSentEmail = useRef(false);

  // Generate verification code when component mounts
  React.useEffect(() => {
    if (!hasSentEmail.current) {
      sendVerificationCode();
      hasSentEmail.current = true;
    }
  }, []);

  const sendVerificationCode = async () => {
    const code = Math.floor(100000 + Math.random() * 900000).toString();
    setCurrentCode(code);
    
    try {
      // EmailJS configuration - replace with your actual values
      const SERVICE_ID = 'service_bzddknq';
      const TEMPLATE_ID = 'template_2mronur';
      const PUBLIC_KEY = 'FAE_nIAcE1v619yjf';
      
      const templateParams = {
        email: email,
        to_email: email,
        recipient_email: email,
        to_name: email.split('@')[0], // Extract name from email
        verification_code: code,
        code: code,
        from_name: 'Orbit Tracker',
        message: `Your verification code is: ${code}`
      };
      
      console.log('Sending email with params:', templateParams);
      
      // Send email using EmailJS
      await emailjs.send(SERVICE_ID, TEMPLATE_ID, templateParams, PUBLIC_KEY);
      
      // Store code in localStorage as backup
      localStorage.setItem(`verification_code_${email}`, code);
      
    } catch (error) {
      console.error('EmailJS error:', error);
      // Fallback to localStorage if EmailJS fails
      localStorage.setItem(`verification_code_${email}`, code);
      setNotification({ 
        message: 'Email service unavailable. Code stored locally for testing.', 
        type: 'warning' 
      });
    }
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    setError('');

    // Check if the entered code matches the sent code
    const storedCode = localStorage.getItem(`verification_code_${email}`);
    if (verificationCode === storedCode) {
      onVerified();
      setNotification({ message: 'Email verified successfully!', type: 'success' });
      // Clean up stored code
      localStorage.removeItem(`verification_code_${email}`);
    } else {
      setError('Invalid verification code. Please check your email and try again.');
    }
  };

  const handleResend = async () => {
    setIsResending(true);
    setError('');
    
    await sendVerificationCode();
    
    setTimeout(() => {
      setIsResending(false);
      setNotification({ message: 'Verification code sent to your email!', type: 'success' });
    }, 1500);
  };

  const showNotification = (message, type) => {
    setNotification({ message, type });
    setTimeout(() => setNotification(null), 3000);
  };

  return (
    <div className="auth-container">
      <div className="auth-card">
        <div className="auth-header">
          <h1>Verify Email</h1>
          <p>We sent a verification code to {email}</p>
          <div className="email-info">
            <p className="email-note">Please check your inbox (including spam folder) for the verification code.</p>
            <p className="dev-note">Note: Configure EmailJS credentials in EmailVerification.js to send real emails</p>
          </div>
        </div>
        
        <form className="auth-form" onSubmit={handleSubmit}>
          {error && <div className="auth-error">{error}</div>}
          
          <div className="form-group">
            <label>Verification Code</label>
            <input
              type="text"
              value={verificationCode}
              onChange={(e) => setVerificationCode(e.target.value.replace(/\D/g, '').slice(0, 6))}
              placeholder="Enter 6-digit code"
              maxLength="6"
              required
              className="verification-input"
            />
            <div className="verification-hint">
              Enter the 6-digit code sent to your email
            </div>
          </div>
          
          <button type="submit" className="btn-primary auth-btn" disabled={verificationCode.length !== 6}>
            Verify Email
          </button>
          
          <button 
            type="button" 
            className="btn-secondary auth-btn" 
            onClick={handleResend}
            disabled={isResending}
          >
            {isResending ? 'Sending...' : 'Resend Code'}
          </button>
        </form>
        
        <div className="auth-footer">
          <button type="button" className="auth-link" onClick={onBack}>
            Back to Signup
          </button>
        </div>
      </div>
      
      {notification && (
        <Notification 
          message={notification.message} 
          type={notification.type}
        />
      )}
    </div>
  );
}

export default EmailVerification;
