import React, { useEffect, useState } from 'react';
import { Chart as ChartJS, ArcElement, CategoryScale, LinearScale, BarElement, Tooltip, Legend } from 'chart.js';
import { Pie, Doughnut, Bar } from 'react-chartjs-2';
import StatisticsCards from './StatisticsCards';
import CategoryBreakdown from './CategoryBreakdown';

ChartJS.register(ArcElement, CategoryScale, LinearScale, BarElement, Tooltip, Legend);

function Analytics({ expenses, timeFilter, onTimeFilterChange, chartColors, showNotification }) {
  const [chartData, setChartData] = useState(null);

  useEffect(() => {
    updateCharts();
  }, [expenses]);

  const updateCharts = () => {
    if (!expenses || !Array.isArray(expenses)) {
      setChartData({
        categoryLabels: [],
        categoryData: [],
        dailyDates: [],
        dailyAmounts: []
      });
      return;
    }

    const categories = {};
    expenses.forEach(e => {
      if (e && e.category) {
        categories[e.category] = (categories[e.category] || 0) + (e.amount || 0);
      }
    });

    const categoryLabels = Object.keys(categories);
    const categoryData = Object.values(categories);

    // Daily spending data
    const dailyData = {};
    expenses.forEach(e => {
      if (e.date) {
        const date = e.date;
        dailyData[date] = (dailyData[date] || 0) + (e.amount || 0);
      }
    });

    const sortedDates = Object.keys(dailyData).sort();
    const dailyAmounts = sortedDates.map(date => dailyData[date]);
    const formattedDates = sortedDates.map(date => {
      try {
        const d = new Date(date);
        return isNaN(d.getTime()) ? date : d.toLocaleDateString('en-IN', { month: 'short', day: 'numeric' });
      } catch {
        return date;
      }
    });

    setChartData({
      categoryLabels,
      categoryData,
      dailyDates: formattedDates,
      dailyAmounts
    });
  };

  if (!chartData) return <div>Loading...</div>;

  const pieChartData = {
    labels: chartData.categoryLabels,
    datasets: [{
      data: chartData.categoryData,
      backgroundColor: chartColors.primary,
      borderWidth: 2,
      borderColor: '#0f172a'
    }]
  };

  const doughnutChartData = {
    labels: chartData.categoryLabels,
    datasets: [{
      data: chartData.categoryData,
      backgroundColor: chartColors.primary,
      borderWidth: 2,
      borderColor: '#0f172a'
    }]
  };

  const barChartData = {
    labels: chartData.dailyDates,
    datasets: [{
      label: 'Daily Spending',
      data: chartData.dailyAmounts,
      backgroundColor: chartColors.gradients,
      borderColor: chartColors.primary,
      borderWidth: 2,
      borderRadius: 8
    }]
  };

  const horizontalBarChartData = {
    labels: chartData.categoryLabels,
    datasets: [{
      label: 'Amount Spent',
      data: chartData.categoryData,
      backgroundColor: chartColors.gradients,
      borderColor: chartColors.primary,
      borderWidth: 2,
      borderRadius: 8
    }]
  };

  const chartOptions = {
    responsive: true,
    maintainAspectRatio: false,
    plugins: {
      legend: {
        position: 'bottom',
        labels: {
          color: '#94a3b8',
          padding: 15,
          font: { size: 12 }
        }
      },
      tooltip: {
        callbacks: {
          label: function (context) {
            const label = context.label || '';
            const value = context.parsed || 0;
            const total = context.dataset.data.reduce((a, b) => a + b, 0);
            const percentage = ((value / total) * 100).toFixed(1);
            return `${label}: ${value.toLocaleString('en-IN')} (${percentage}%)`;
          }
        }
      }
    }
  };

  const barChartOptions = {
    indexAxis: 'x',
    responsive: true,
    maintainAspectRatio: false,
    scales: {
      y: {
        beginAtZero: true,
        ticks: {
          color: '#94a3b8',
          callback: function (value) {
            return '' + value.toLocaleString('en-IN');
          }
        },
        grid: {
          color: 'rgba(255, 255, 255, 0.1)'
        }
      },
      x: {
        ticks: { color: '#94a3b8' },
        grid: {
          color: 'rgba(255, 255, 255, 0.05)'
        }
      }
    },
    plugins: {
      legend: {
        display: false
      },
      tooltip: {
        callbacks: {
          label: function (context) {
            return 'Spent: ' + context.parsed.y.toLocaleString('en-IN');
          }
        }
      }
    }
  };

  const horizontalBarChartOptions = {
    indexAxis: 'y',
    responsive: true,
    maintainAspectRatio: false,
    scales: {
      x: {
        beginAtZero: true,
        ticks: {
          color: '#94a3b8',
          callback: function (value) {
            return '' + value.toLocaleString('en-IN');
          }
        },
        grid: {
          color: 'rgba(255, 255, 255, 0.1)'
        }
      },
      y: {
        ticks: { color: '#94a3b8' },
        grid: {
          color: 'rgba(255, 255, 255, 0.05)'
        }
      }
    },
    plugins: {
      legend: {
        display: false
      },
      tooltip: {
        callbacks: {
          label: function (context) {
            return 'Spent: ' + context.parsed.x.toLocaleString('en-IN');
          }
        }
      }
    }
  };

  return (
    <section className="analytics-section">
      <div className="section-header">
        <h2>Analytics Dashboard</h2>
        <div className="filter-controls">
          <select 
            id="time-filter" 
            className="filter-select"
            value={timeFilter}
            onChange={(e) => {
              onTimeFilterChange(e.target.value);
              showNotification(`Filter applied: ${e.target.options[e.target.selectedIndex].text}`, 'info');
            }}
          >
            <option value="all">All Time</option>
            <option value="week">This Week</option>
            <option value="month">This Month</option>
            <option value="year">This Year</option>
          </select>
        </div>
      </div>

      <StatisticsCards expenses={expenses} />

      <div className="charts-grid">
        <div className="chart-container">
          <h3>Spending by Category (Pie)</h3>
          <div className="chart-wrapper-large">
            <Pie data={pieChartData} options={chartOptions} />
          </div>
        </div>

        <div className="chart-container">
          <h3>Spending by Category (Doughnut)</h3>
          <div className="chart-wrapper-large">
            <Doughnut data={doughnutChartData} options={chartOptions} />
          </div>
        </div>
      </div>

      <div className="charts-grid-full">
        <div className="chart-container">
          <h3>Daily Spending Over Time</h3>
          <div className="chart-wrapper-large">
            <Bar data={barChartData} options={barChartOptions} />
          </div>
        </div>
      </div>

      <div className="charts-grid-full">
        <div className="chart-container">
          <h3>Category Comparison</h3>
          <div className="chart-wrapper-large">
            <Bar data={horizontalBarChartData} options={horizontalBarChartOptions} />
          </div>
        </div>
      </div>

      <CategoryBreakdown expenses={expenses} chartColors={chartColors} />
    </section>
  );
}

export default Analytics;
