import React, { useState } from 'react';
import { useAuth } from '../contexts/AuthContext';
import Notification from './Notification';
import EmailVerification from './EmailVerification';

function Signup({ onSwitchToLogin }) {
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [error, setError] = useState('');
  const [notification, setNotification] = useState(null);
  const [showVerification, setShowVerification] = useState(false);
  const [pendingUser, setPendingUser] = useState(null);
  const { signup } = useAuth();

  const handleSubmit = async (e) => {
    e.preventDefault();
    setError('');
    
    if (password !== confirmPassword) {
      setError('Passwords do not match');
      return;
    }
    
    if (password.length < 6) {
      setError('Password must be at least 6 characters');
      return;
    }
    
    // Store user data and show verification screen
    setPendingUser({ name, email, password });
    setShowVerification(true);
    setNotification({ message: 'Please check your email for verification code', type: 'info' });
  };

  const handleVerified = async () => {
    if (pendingUser) {
      const result = await signup(pendingUser.name, pendingUser.email, pendingUser.password);
      
      if (result.success) {
        setNotification({ message: 'Account created successfully!', type: 'success' });
      } else {
        setError(result.error);
        setShowVerification(false);
      }
    }
  };

  const handleBackToSignup = () => {
    setShowVerification(false);
    setPendingUser(null);
  };

  const showNotification = (message, type) => {
    setNotification({ message, type });
    setTimeout(() => setNotification(null), 3000);
  };

  if (showVerification && pendingUser) {
    return (
      <EmailVerification 
        email={pendingUser.email}
        onVerified={handleVerified}
        onBack={handleBackToSignup}
      />
    );
  }

  return (
    <div className="auth-container">
      <div className="auth-card">
        <div className="auth-header">
          <h1>Orbit <span className="accent">Tracker</span></h1>
          <p>Join Cosmic Expense Analytics</p>
        </div>
        
        <form className="auth-form" onSubmit={handleSubmit}>
          {error && <div className="auth-error">{error}</div>}
          
          <div className="form-group">
            <label>Name</label>
            <input
              type="text"
              value={name}
              onChange={(e) => setName(e.target.value)}
              placeholder="Enter your name"
              required
            />
          </div>
          
          <div className="form-group">
            <label>Email</label>
            <input
              type="email"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              placeholder="Enter your email"
              required
            />
          </div>
          
          <div className="form-group">
            <label>Password</label>
            <input
              type="password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              placeholder="Create a password (min 6 chars)"
              required
              minLength="6"
            />
          </div>
          
          <div className="form-group">
            <label>Confirm Password</label>
            <input
              type="password"
              value={confirmPassword}
              onChange={(e) => setConfirmPassword(e.target.value)}
              placeholder="Confirm your password"
              required
              minLength="6"
            />
          </div>
          
          <button type="submit" className="btn-primary auth-btn">
            Sign Up
          </button>
        </form>
        
        <div className="auth-footer">
          <p>
            Already have an account?{' '}
            <button type="button" className="auth-link" onClick={onSwitchToLogin}>
              Login
            </button>
          </p>
        </div>
      </div>
      
      {notification && (
        <Notification 
          message={notification.message} 
          type={notification.type}
        />
      )}
    </div>
  );
}

export default Signup;
