import React from 'react';

function ExpenseList({ expenses, onDeleteExpense }) {
  const formatter = new Intl.NumberFormat('en-IN', {
    style: 'currency',
    currency: 'INR'
  });

  const total = expenses.reduce((sum, e) => sum + e.amount, 0);

  return (
    <section className="history-section">
      <div className="glass-panel">
        <div className="list-header">
          <h2>Transaction History</h2>
          <div className="total-display">Total: {formatter.format(total)}</div>
        </div>
        <ul id="expense-list">
          {expenses.length === 0 ? (
            <li className="no-data">No expenses to display</li>
          ) : (
            expenses.map(expense => (
              <li key={expense.id} className="expense-item">
                <div className="expense-info">
                  <strong>{expense.description}</strong>
                  <span>{expense.date}  {expense.category}</span>
                </div>
                <div className="expense-actions">
                  <span className="expense-amount">{formatter.format(expense.amount)}</span>
                  <button 
                    className="delete-btn"
                    onClick={() => onDeleteExpense(expense.id)}
                  >
                    
                  </button>
                </div>
              </li>
            ))
          )}
        </ul>
      </div>
    </section>
  );
}

export default ExpenseList;
